<template>
  <div class="upload-input" :class="{ disabled: fileList.length >= props.maxCount }">
    <a-upload-dragger
      :file-list="fileList"
      name="file"
      :multiple="true"
      :max-count="props.maxCount"
      :accept="getAccept"
      :beforeUpload="beforeUpload"
      @remove="handleRemove"
      class="ant-upload-drag"
    >
      <div class="ant-upload-drag-icon">
        <inbox-outlined></inbox-outlined>
      </div>
      <div class="ant-upload-text">{{ t('click_or_drag_upload') }}</div>
      <div class="ant-upload-hint">
        <p>{{ t('batch_upload_hint', { count: props.maxCount }) }}</p>
        <p>
          <span>{{ t('supported_file_types') }}</span
          ><span class="ant-upload-hint-ext" v-for="ext in fileTypes" :key="ext">.{{ ext }}</span>
        </p>
      </div>
    </a-upload-dragger>

    <div class="disabled-mask" @click="showMaxCountErrorTip"></div>
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue'
import { message } from 'ant-design-vue'
import { InboxOutlined } from '@ant-design/icons-vue'
import { useI18n } from '@/hooks/web/useI18n'

const { t } = useI18n('views.ai-extract-faq.list.components.upload-input')

const emit = defineEmits(['change', 'update:value'])

const props = defineProps({
  value: {
    type: Array,
    default: () => []
  },
  maxCount: {
    type: Number,
    default: 1
  },
  type: {
    type: [String, Number],
    default: 0
  }
})

let fileTypes = ['docx', 'txt', 'md']

const getAccept = computed(() => {
  return fileTypes.map((item) => `.${item}`).join(',')
})

const fileList = ref([])

watch(
  () => props.value,
  (val) => {
    fileList.value = val
  },
  {
    immediate: true
  }
)

let timer = null

const showMaxCountErrorTip = () => {
  if (fileList.value.length >= props.maxCount) {
    return message.error(t('max_upload_error', { count: props.maxCount }))
  }
}

const onChange = () => {
  if (fileList.value.length > props.maxCount) {
    message.error(t('max_upload_error', { count: props.maxCount }))
  }

  let files = [...fileList.value]

  emit('change', files)

  emit('update:value', files)
}

const handleRemove = (file) => {
  removeFile(file)

  onChange()
}

const removeFile = (file) => {
  for (let i = 0; i < fileList.value.length; i++) {
    if (fileList.value[i].uid === file.uid) {
      fileList.value.splice(i, 1)
      break
    }
  }
}

const beforeUpload = (file) => {
  const ext = file.name.split('.').pop()
  const isWhitelistFile = fileTypes.includes(ext.toLowerCase())

  if (!isWhitelistFile) {
    message.error(t('unsupported_file_format'))
    return false
  }

  const isLt100M = file.size / 1024 / 1024 < 100

  if (!isLt100M) {
    message.error(t('file_size_limit'))
    return false
  }

  fileList.value = [...(fileList.value || []), file]

  if (fileList.value.length > props.maxCount) {
    fileList.value = fileList.value.splice(0, props.maxCount)
  }

  if (timer) {
    clearTimeout(timer)
  }

  timer = setTimeout(() => {
    onChange()
    clearTimeout(timer)
  }, 50)

  return false
}
</script>

<style lang="less" scoped>
.ant-upload-drag {
  .ant-upload-drag-icon {
    font-size: 48px;
    color: #2475fc;
  }

  .ant-upload-text {
    line-height: 22px;
    font-size: 14px;
    text-align: center;
    color: #000000;
  }

  .ant-upload-hint {
    line-height: 20px;
    margin-top: 4px;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.45);
    text-align: center;
  }
}

.upload-input {
  .ant-upload-hint-ext {
    padding-right: 4px;
  }

  .disabled-mask {
    display: none;
    cursor: pointer;
    height: 176px;
  }

  &.disabled {
    position: relative;

    .disabled-mask {
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0;
    }
  }
}
</style>
